.App {
  width: 100%;
  min-height: 100vh;
}

.sidebar {
  min-width: 350px;
  padding: 20px;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}
.msidebar {
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}
.card-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-height: calc(100vh - 40px);
  overflow-y: auto;
}

/* 스크롤바 스타일링 (선택사항) */
.card-container::-webkit-scrollbar {
  width: 6px;
}

.card-container::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.card-container::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 3px;
}

.App {
  position: relative;
  width: 100%;
  height: 100vh; /* 화면 전체를 차지하도록 설정 */
  background-size: cover; /* 이미지가 전체 화면을 덮도록 설정 */
  background-position: center; /* 이미지를 중앙에 배치 */
  background-attachment: fixed; /* 스크롤시 배경이 고정되도록 설정 */
}
