.userCard {
  width: 155px;
  height: 155px;
  padding: 20px;
  background-color: rgba(14, 14, 14, 0.7);
  box-sizing: border-box;
  border-radius: 15px;
  float: left;
}
.pictureCard {
  width: 155px;
  height: 155px;
  box-sizing: border-box;
  border-radius: 15px;
  float: left;
  position: relative;
}
.Image {
  width: 155px;
  height: 155px;
  object-fit: fill;
  border-radius: 15px;
}
.userCard input {
  top: 20px;
  left: 15px;
  background: transparent;
  border: none;
  font-family: "Times New Roman", Times, serif;
  font-size: 16px;
  border-bottom: 1px solid #555555;
  outline: none;
  padding: 10px;
  color: white;
}

.WatherCard {
  width: 330px;
  padding: 10px 20px;
  margin: 15px 0;
  background-color: rgba(14, 14, 14, 0.7);
  box-sizing: border-box;
  border-radius: 15px;
  float: left;
}

.wather-container {
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wather-slider {
  width: 100%;
  height: 8px;
  -webkit-appearance: none;
  background: #ddd;
  outline: none;
  border-radius: 5px;
  transition: background 0.3s ease;
}

.wather-slider::-webkit-slider-runnable-track {
  background: #ddd;
  height: 8px;
  border-radius: 5px;
}
.wather-slider[data-thumb="drink_0"]::-webkit-slider-thumb {
  background-image: var(--drink-0-bg-image);
}

.wather-slider[data-thumb="drink_1"]::-webkit-slider-thumb {
  background-image: var(--drink-1-bg-image);
}

.wather-slider[data-thumb="drink_2"]::-webkit-slider-thumb {
  background-image: var(--drink-2-bg-image);
}

.wather-slider[data-thumb="drink_3"]::-webkit-slider-thumb {
  background-image: var(--drink-3-bg-image);
}

.wather-slider[data-thumb="drink_4"]::-webkit-slider-thumb {
  background-image: var(--drink-4-bg-image);
}
.wather-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -10px;
}

.wather-slider::-moz-wather-track {
  background: #ddd;
  height: 8px;
  border-radius: 5px;
}

.wather-labels {
  margin-top: 10px;
}

.water-img {
  display: flex;
  flex-direction: row;
}

.water-img img {
  width: 30px;
}

.todoListCard {
  width: 330px;
  padding: 30px 30px;
  background-color: rgba(14, 14, 14, 0.7);
  border-radius: 15px;
  color: #fff;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.todoInput {
  width: 100%;
  height: 40px;
  font-size: 18px;
  background-color: transparent;
  box-sizing: border-box;
  border: none;
  text-align: left;
  padding: 5px 10px;
  color: #fff;
  font-family: "Times New Roman", Times, serif;
}

.time-container {
  text-align: right;
  padding: 20px;
  color: #979797;
  font-family: Arial, sans-serif;
  position: fixed;
  top: 7%;
  right: 8%;
}
.mtime-container {
  padding: 20px;
  color: #979797;
  font-family: Arial, sans-serif;
}

.time {
  font-size: 64px;
  font-weight: bold;
  margin-bottom: 10px;
}

.temperature-info {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 5px;
  font-size: 14px;
}

.temperature-info span {
  display: block;
}
.loading-spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  color: #00000075;
  z-index: 9999;
}

.todoListCard .state {
  text-align: right;
  font-size: 16px;
  margin-bottom: 10px;
  display: flex;
  align-items: end;
  justify-content: end;
}

.list {
  margin-right: 5px;
  margin-left: 5px;
}
li.checked span:first-child {
  color: yellow;
}
.checked div span:nth-child(2) {
  text-decoration-line: line-through;
}
li span:last-child {
  text-align: left;
}
/* Overlay */
.overlay {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal */
.modal {
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.3s ease-in-out;
}

/* Modal Content */
.modal-content h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 15px;
  color: #333;
}

.modal-content p {
  font-size: 1rem;
  color: #555;
  margin-bottom: 20px;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
}

.btn {
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  margin: 10px;
}

.cancel-btn {
  background-color: #f44336;
  color: white;
}

.confirm-btn {
  background-color: #4caf50;
  color: white;
}

.cancel-btn:hover {
  background-color: #d32f2f;
}

.confirm-btn:hover {
  background-color: #388e3c;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
